.homeHead {
  height: 1.066667rem;
  background-color: #fff;
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: space-around;
}

.homeHead .homeHead-left span.iconfont.icon-dizhi {
  color: #d5f;
}

.homeHead .homeHead-left select {
  border: none;
  outline: none;
}

.homeHead .homeHead-center {
  position: relative;
}

.homeHead .homeHead-center .icon-sousuo {
  position: absolute;
  left: 30%;
  top: 0.026667rem;
}

.homeHead .homeHead-center input.search {
  border-radius: 0.266667rem;
  border: 0.026667rem #ccc solid;
  width: 6rem;
  height: 0.666667rem;
  font-size: 0.32rem;
  padding-left: 2.666667rem;
}

.homeHead .homeHead-right span {
  color: #d5f;
  font-size: 0.533333rem;
}